import './Gallery.css';
import React from 'react';
import Header from '../Header/Header';
import CardGrid from './../CardGrid/CardGrid';
import { connect } from 'react-redux';
import { getModels, selectedPreviewModel } from './../../actions';

class Gallery extends React.Component {
  componentDidMount = () => {
    this.props.getModels();
  };

  render() {
    return (
      <>
        <Header />
        <div className="gallery-container">
          <CardGrid
            selectedPreviewModel={this.props.selectedPreviewModel}
            models={this.props.models}
          />
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    models: state.model.models,
    preview: state.model.selectedPreviewModel,
  };
};

export default connect(mapStateToProps, { getModels, selectedPreviewModel })(
  Gallery
);
