import './Account.css';
import React from 'react';
import google from './../../../media/images/google.png';

const Account = () => {
  return (
    <ul className="account-dropdown-menu-inactive" id="account-dropdown-menu">
      <li className="account-dropdown-items" style={{ padding: '10px' }}>
        <div
          style={{
            border: '1px solid white',
            borderRadius: '5px',
            background: 'white',
            display: 'flex',
            justifyContent: 'center',
            padding: '5px',
            color: 'black',
          }}
        >
          <div
            style={{
              height: '20px',
              width: '20px',
            }}
          >
            <img src={google} />
          </div>
          <div style={{ paddingLeft: '10px', paddingRight: '15px' }}>
            <p>Log In</p>
          </div>
        </div>
      </li>
      <li
        className="account-dropdown-items"
        onClick={() =>
          window.open(
            `${window.location.protocol}//${window.location.host}/MyCollection`,
            '_self'
          )
        }
      >
        My Collection
      </li>
    </ul>
  );
};

export default Account;
